        text-align: center;
      }
    }
  }
  .up{
    margin-bottom: 40px;
  }
  .bottombar {
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 10px 5%;
    // margin-top: 10px;
    display: flex;
    justify-content: space-between;
    height: 3rem;
    background-color: rgb(250, 248, 248);
    .collect {
      height: 30px;
      width: 25%;
