      text-align: center;
      background-color: rgb(207, 29, 29);
      border-radius: 10px;
      span {
        line-height: 30px;
        font-size: 20px;
        color: #fff;
      }
    }
    .online,
    .phonechart {
      height: 30px;
      width: 35%;
      text-align: center;
      // padding: 5px 0;
      span {
        line-height: 30px;
        font-size: 20px;
        color: #fff;
      }
    }
    .online {
      background-color: orangered;
      border-radius: 10px;
    }
    .phonechart {
      background-color: #007d65;
      border-radius: 10px;
